import React from "react";
import { useDispatch } from "react-redux";
import { AddTocart, RemoveToCart } from "../state/counterSlice";

const Card = ({ products, removeCartButton }) => {
  // const cart = useSelector((state) => state.cart);
  const dispatch = useDispatch();

  return (
    <>
      {products?.map((item) => {
        const { id, title, thumbnail, image, price } = item;
        return (
          <div className="card" key={id}>
            <img src={thumbnail || image} className="card-image" alt={id} />
            <h2 className="card-title">{title}</h2>
            <p className="card-text">{`INR. ${price}`}</p>
            {!removeCartButton && (
              <button onClick={() => dispatch(AddTocart(item))}>
                Add To Cart
              </button>
            )}
            {removeCartButton && (
              <button onClick={() => dispatch(RemoveToCart(id))}>Remove</button>
            )}
          </div>
        );
      })}
    </>
  );
};

export default Card;
